import { PAGE_SIZE } from '@/assets/config'
import { CODE, approvalKeys } from '@/assets/constant'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'annualBriefing',
  data () {
    return {
      state: 0,
      chartList: [
        { elmId: 'chart-1', title: '论文发表情况 - 中文', key: 'journal_chinese_paper', selectName: '中文论文' },
        { elmId: 'chart-2', title: '论文发表情况 - 英文', key: 'journal_english_paper', selectName: '英文论文' },
        { elmId: 'chart-3', title: '获批项目经费', key: 'funds_of_res_pro', selectName: '获批项目经费' },
        { elmId: 'chart-8', title: '获批项目数量', key: 'num_of_res_pro', selectName: '获批项目数量' },
        { elmId: 'chart-4', title: '科研获奖 - 国家级项目', key: 'num_of_res_awa_cou', selectName: '国家级项目' },
        { elmId: 'chart-5', title: '科研获奖 - 省级项目', key: 'num_of_res_awa_province', selectName: '省级项目' },
        { elmId: 'chart-6', title: '科研获奖 - 部级项目', key: 'num_of_res_awa_min', selectName: '部级项目' },
        { elmId: 'chart-7', title: '科研获奖 - 全部项目', key: 'num_of_res_awa', selectName: '全部项目' }
      ],
      condition: {},
      searchOptions: [
        { label: '时间', key: 'time', type: 'date' },
        { label: '名称', key: 'name' }
      ],
      model: '',
      selectOption: [],
      canExport: Cache.hasPermission('management_work')
    }
  },
  methods: {
    getData () {
      const request = {}
      for (const key in this.condition) {
        request[key] = this.condition[key]
      }
      Service.StatisticsChart.get(request).then(response => {
        if (response.code == 200) {
          this.chartList.map(chart => {
            const chartData = response.data[chart.key]
            chartData['elmId'] = chart.elmId
            this.setChart(chartData)
          })
        }
      })
    },
    publicSearch (condition) {
      this.condition = {}
      for (const key in condition) {
        if (condition[key]) {
          this.condition[key] = condition[key]
        }
      }
      this.getData()
    },
    selectTag (state) {
      if (state == this.state) {
        return
      }
      this.setChartOptions(state)
      this.state = state
    },
    setChartOptions (state) {
      if (state > 0) {
        switch (Number(state)) {
          case 1:
            this.selectOption = ['中文论文', '英文论文']
            break
          case 2:
            this.selectOption = ['获批项目经费', '获批项目数量']
            break
          case 3:
            this.selectOption = ['国家级项目', '省级项目', '部级项目', '全部项目']
            break
        }
      } else {
        this.selectOption = []
      }
    },
    canShow (chart) {
      if (this.state == 0) {
        return true
      } else {
        if (this.model == '') {
          if ($.inArray(chart.selectName, this.selectOption) > -1) {
            return true
          }
        } else {
          if (chart.selectName == this.model) {
            return true
          }
        }
      }
      return false
    },
    savePDF() {
      $('.super').remove()
      let targetDom = $("#chartBox");
      let copyDom = $("<div/>");
      copyDom.addClass('super');
      copyDom.width(targetDom.width() + "px");
      copyDom.height(targetDom.height() + "px");
      $('body').append(copyDom);

      html2canvas($('#chartBox'), {
        allowTaint: true,
        height: $('#chartBox').outerHeight(),
        background : '#ffffff',
        onrendered: function(canvas) {

          let contentWidth = canvas.width;
          let contentHeight = canvas.height;

          //一页pdf显示html页面生成的canvas高度;
          let pageHeight = contentWidth / 592.28 * 841.89;
          //未生成pdf的html页面高度
          let leftHeight = contentHeight;
          //页面偏移
          let position = 0;
          //a4纸的尺寸[595.28,841.89]，html页面生成的canvas在pdf中图片的宽高

          let imgWidth = 595.28;
          let imgHeight = 592.28/contentWidth * contentHeight;

          if ($('.chart[style="display: none;"]').length == $('.chart').length - 1) {  // 如果图表数量为1, 放大图表
            imgWidth = imgWidth * 2
            imgHeight = imgHeight * 2
          }
          // 以0.84的比例缩放大小
          imgWidth = imgWidth * 0.84
          imgHeight = imgHeight * 0.84

          let pageData = canvas.toDataURL('image/jpeg', 1.0);

          let pdf = new jsPDF('', 'pt', 'a4');

          //有两个高度需要区分，一个是html页面的实际高度，和生成pdf的页面高度(841.89)
          //当内容未超过pdf一页显示的范围，无需分页
          if (leftHeight < pageHeight) {
            pdf.addImage(pageData, 'JPEG', 20, 20, imgWidth, imgHeight );
          } else {
            while(leftHeight > 0) {
              pdf.addImage(pageData, 'JPEG', 20, position, imgWidth, imgHeight)
              leftHeight -= pageHeight;
              position -= 841.89;
              //避免添加空白页
              if(leftHeight > 0) {
                pdf.addPage();
              }
            }
          }
          pdf.save('年度简报.pdf');
        }
      })
    },
    setChart (chartData) {
      var Chart = echarts.init(document.getElementById(chartData.elmId))
      var option = {
        backgroundColor: 'white',
        legend: {
          data: []
        },
        grid:{
          x:35,
          y:45,
          x2:35,
          y2:20,
          borderWidth:1
        },
        xAxis: {
          data: chartData.xData
        },
        yAxis: {
          name: chartData.name
        },
        series: [{
          name: chartData.name,
          type: 'bar',
          data: chartData.data
        }]
      };
      Chart.setOption(option)
    }
  },
  created () {
    this.getData()
  },
  mounted () {

  }
}



